html{
  box-sizing: border-box;
  height: 100%;
}

*,
*::after,
*::before{
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  align-items: center;
  justify-content: center;
  height: inherit;
  display: flex;
  background:linear-gradient(330deg, #6388d8, #e1cfe2, #bd41bd);
  font-family: -apple-system, BlinkMacSystemFont, 'Dosis', 'Roboto', 'Segoe UI',  'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-display: swap;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
