.screen-focus{
  background:none;
  border:none;
  outline: none;
}

.calc{
  backdrop-filter: blur(5.5px);
  border: 1px solid rgab(255, 255, 255, 0.01);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgab(35, 35, 35, 0.01);
  color: #232323;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  background: rgba(255, 255, 255, 0.30);
  border: 1px solid rgba(255, 255, 255, 0.34);
  flex-basis: 400px;
  height: 36rem;
  padding: 1.5rem 2rem;
}

.calc-result{
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
  background: rgba(255, 255, 255, 0.75);
  border: 1px solid rgba(255, 255, 255, 0.75);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(35, 35, 35, 0.1);
  color: #232323;
  overflow: auto;
  padding: 1rem;
  width: 20rem;
  text-align: right;
}

.calc-result-show{
  font-size: 0.75rem;
  height: 1rem;
  text-align: inherit;
  padding-right: 1rem;
  padding-bottom: 1rem;
}

.calc-result-input{
  padding-right: 0.25rem;
  color: #232323;
  font-size: 2.75rem;
}

.calc-button-row{
  display: flex;
  justify-content: space-between;
  margin: 5% 0;
}

.calc-button{
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
  background: rgba(255, 255, 255, 0.75);
  border: 1px solid rgba(255, 255, 255, 0.01);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(35, 35, 35, 0.1);
  color:#232323;
  flex-basis: 20%;
  font-family: inherit;
  font-size: 2rem;
  height: 4rem;
} 

.button-double{
  flex-basis: 47%;
}


.calc-button:last-child{
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
  border: 1px solid rgba(255, 255, 255, 0.01);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(35, 35, 35, 0.1);
  color:#fff;
  background: #1cd8c894;
}

.equals{
  background-color: #f700ff4f !important;
}

.calc-button:last-child:hover{
  background-color: #1cd8c82f;
  color: inherit;
}
.calc-button:last-child:active{
  background-color: #23232360 !important;
  color: inherit;
}

.calc-button:hover{
  background-color: inherit;
}

.calc-button:active{
  background-color: #23232360;
}